@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}

body {
  height: 100vh;
  color: #78909c;
  background: linear-gradient(
    135deg,
    #65bde0 0%,
    #65bde0 50%,
    #fa3c3c 50%,
    #fa3c3c 100%
  );
}

.DadList {
  padding-right: 20px;
}

.DadList::-webkit-scrollbar {
  width: 10px;
  background-color: rgb(230, 230, 230);
}

.DadList::-webkit-scrollbar-thumb {
  background: linear-gradient(to top, #008aff, #00ffe7);
  border-radius: 1rem;
}

.DadList::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.ScrollBar {
  position: fixed;
  top: 0;
  right: 0;
  width: 10px;
  background: linear-gradient(to top, #008aff, #00ffe7);
  -webkit-animation: hue 5s linear infinite;
          animation: hue 5s linear infinite;
}

@-webkit-keyframes hue {
  0% {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }

  100% {
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

@keyframes hue {
  0% {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }

  100% {
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

.Joke {
  display: flex;
  align-items: center;
  border-bottom: 2px solid #eeeeee;
  padding: 0.2rem;
  width: 100%;
}

.JokeButtons {
  display: grid;
  place-content: center;
  width: 15%;
}

.JokeText {
  width: 75%;
  font-size: 0.9rem;
}

.JokeVotes {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  font-size: 0.9rem;
  display: grid;
  place-content: center;
  border: 2px solid var(--border);
  transition: border .5s;
}

.JokeButtons .feather {
  cursor: pointer;
  margin: 5px auto;
}

.JokeEmoji {
  font-size: 3vw;
  transition: 0.5s;
  margin: 10px auto;
}

.JokeEmoji:hover {
  transform: rotateZ(10deg);
}

.Loader-Wrapper{
  display: grid;
  place-content: center;
  position: relative;
  top: 50%;
}

.Loader {
  width: 100px;
  height: 100px;
  border: 10px solid rgb(53, 53, 53);
  border-bottom: 10px solid #3498db;
  border-radius: 50%;
  transition: 0.2s;
  -webkit-animation: rotate 1s linear infinite, hueRotate 4s linear infinite;
          animation: rotate 1s linear infinite, hueRotate 4s linear infinite;
}

@-webkit-keyframes hueRotate{
  to{
    -webkit-filter:hue-rotate(360deg);
            filter:hue-rotate(360deg);
  }
}

@keyframes hueRotate{
  to{
    -webkit-filter:hue-rotate(360deg);
            filter:hue-rotate(360deg);
  }
}
@-webkit-keyframes rotate {
  to {
    transform: rotateZ(360deg);
  }
}
@keyframes rotate {
  to {
    transform: rotateZ(360deg);
  }
}

.DadJokes {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  width: 80vw;
  height: 95vh;
}

.jokeAside {
  display: grid;
  place-items: center;
  height: 100%;
  width: 30%;
  text-align: center;
  background-color: #9575cd;
  box-shadow: 10px 10px 0.5rem rgba(51, 51, 51, 0.281);
  z-index: 2;
}
.jokeAside h1 {
  font-size: 6vw;
  color: white;
  font-weight: 100;
  letter-spacing: 1vw;
  margin: 1rem;
}
.jokeAside h1 span {
  font-weight: 700;
  letter-spacing: 0;
  border-radius: 10px;
}
.jokeAside img {
  width: 50%;
  border-radius: 50%;
  box-shadow: inset 0 0 0 1rem white;
  margin: 1rem;
  transition: 0.3s;
}

.jokeAside img:hover {
  transform: scale(1.1) rotate(-10deg);
}

.jokeButton {
  background-color: #9575cd;
  border: none;
  border-radius: 1rem;
  width: 12vw;
  height: 4vw;
  font-size: 2vw;
  color: white;
  outline: none;
  box-shadow: 1px 1px 0.3rem rgba(0, 0, 0, 0.336),
    -1px -1px 2px rgba(207, 207, 207, 0.5);
  cursor: pointer;
  transition: 0.2s;
  margin-bottom: 5rem;
}
.jokeButton:active {
  transform: translateY(1px);
  box-shadow: 1px 1px 0.1rem rgba(0, 0, 0, 0.336),
    -1px -1px 2px rgba(207, 207, 207, 0.384);
}
.jokeButton:hover {
  background-color: rgba(255, 255, 255, 0.055);
}

.DadList {
  height: 90%;
  width: 100%;
  background-color: white;
  list-style: none;
  overflow-y: scroll;
  position: relative;
}

.App{
    display: grid;
    place-items: center;
}
